import { PaginatedParams } from "@rp/common/utils"

const filesQueryKeys = {
  file: ["file"] as const,
  fileUrl: (fileId: string | undefined) => [...filesQueryKeys.file, fileId, "file-url"] as const,
  paginated: (params?: PaginatedParams) => {
    if (!params) return ["files-paginated"] as const
    const { sortBy, direction, shared, fileName, ownership, owners } = params
    return ["files-paginated", shared, sortBy, direction, fileName, ownership, owners] as const
  },
  metadata: (fileId: string | undefined) => [...filesQueryKeys.file, fileId, "metadata"] as const,
  indexStatus: (fileId: string | undefined) =>
    [...filesQueryKeys.file, fileId, "index-status"] as const,
}

export { filesQueryKeys }
